.tooltip {
    visibility: hidden;
    position: absolute;

    text-align: left;
    padding: 10px;
    background-color: $light-grey;
    opacity: 90%;
    border-radius: 8px;
    border: 1px solid white;
    width: 180px;
    font-weight: lighter;
    margin-top: 4%;

    #country-name {
        font-size: 20px;
        font-weight: bold;
    }

    th {
        padding-right: 6px;
    }

    th, td {
        font-size: 12px;
        padding: 3px;
    }   
}

.tooltip:before {
    content: "";
    position: absolute;
    height: 20px;
    width: 20px;
    top: -11px;
    left: 20px;
    border-left: 1px solid white;
    border-top: 1px solid white;
    transform: rotate(45deg);
    background-color: $light-grey;
    opacity: 90%;
}
